.breadcrumb {
  position: relative;
  z-index: 991;
}
.breadcrumb:not(.below-banner) {
  position: absolute;
  top: 66px;
  width: 100%;
}
@media (min-width: 813px) {
  .breadcrumb:not(.below-banner) {
    top: 90px;
  }
}
.breadcrumb__border {
  border-top: 2px solid #ffffff;
  padding-top: 10px;
  padding-bottom: 10px;
}
.below-banner .breadcrumb__border {
  border-width: 0;
  padding-top: 50px;
}
.breadcrumb li,
.breadcrumb a {
  color: #ffffff;
  font-size: 0.875rem;
  list-style: none;
  text-decoration: none;
}
.breadcrumb a:hover {
  color: #e69159;
}
.breadcrumb li:after {
  display: inline-block;
  vertical-align: middle;
  margin: 0 10px;
  font-size: 1rem;
}
@media (max-width: 640px) {
  .breadcrumb li:after {
    margin: 0 3px;
    font-size: 0.875rem;
  }
}
.breadcrumb li:last-child:after {
  display: none;
}

.btn {
  display: inline-block;
  padding: 12px 33px;
  vertical-align: top;
  color: #ffffff;
  font-size: 1rem;
  line-height: 1.5;
  text-align: center;
  border-radius: 0px;
  background-color: transparent;
  text-decoration: none;
  border: 1px solid #ffffff;
  margin-top: 20px;
  transition: ease all 0.3s;
}

.site__header {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 999;
  border-bottom: 1px solid #ffffff;
}
@media (max-width: 1024px) {
  .site__header {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
.site__header.is-stick {
  position: fixed;
  margin-top: -100px;
  transform: translateY(100px);
  transition: ease transform 0.5s;
  background-color: #121212;
}

.brand {
  display: flex;
  width: 90px;
}
.brand__black {
  display: none;
}
.brand .icon--logo {
  height: 26px;
}

#icon--logo {
  height: 26px;
}
#icon--logo .elem {
  fill: white;
}

.white-theme #icon--logo .elem {
  fill: black;
}

.hamburger {
  display: flex;
  height: 30px;
  width: 30px;
  padding: 0;
  margin: 0;
  background-color: transparent;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
}
.hamburger:hover, .hamburger:focus {
  background-color: transparent;
  outline: none;
