.loading {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(18, 18, 18, 0.95);
  z-index: 9999;
}
.loading__wrap {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loading__ellipsis {
  display: inline-block;
  position: relative;
  width: 130px;
  height: 65px;
}
.loading__dots {
  position: relative;
  width: 100px;
  height: 23px;
  margin: auto;
  display: block;
}
.loading__dots span {
  position: absolute;
  bottom: 0;
  width: 16px;
  height: 16px;
  border-radius: 100%;
  background: #fff;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.loading__dots span:nth-child(1) {
  left: 8px;
  animation: lds-ellipsis1 0.6s infinite;
}
.loading__dots span:nth-child(2) {
  left: 8px;
  animation: lds-ellipsis2 0.6s infinite;
}
.loading__dots span:nth-child(3) {
  left: 32px;
  animation: lds-ellipsis2 0.6s infinite;
}
.loading__dots span:nth-child(4) {
  left: 56px;
  animation: lds-ellipsis2 0.6s infinite;
}
.loading__dots span:nth-child(5) {
  left: 80px;
  animation: lds-ellipsis4 0.6s infinite;
}
@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes lds-ellipsis4 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}

