// hero banner
.herobanner {
    position: relative;
    background-size: cover;
    background-position: center;

    @include respond-above(md) {
        height: 100vh;

        &.full-width-after-header {
            height: calc(100vh - 150px);
        }
    }
    @media (min-width: 768px) and (max-height: 767px){  
        height: auto;
    }

    &:after {
        position: absolute;
        content: '';
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background-color: $color--grey-1;
        opacity: 0.70;
    }

    &.fit-content{
        background-size: contain;
        background-repeat: no-repeat;
        min-height: 40vh;
    }

    &.no-blur {
        &:after {
            display: none;
        }
    }

    @include respond-above(md) {
        &.no-overlay {
            /*h1, p{
                color: $color--grey-1;
            }*/
            &:after{
                display: none;
            }
        }
    }

    &__caption {
        padding-top: 100px;
        padding-bottom: 50px;
        width: 100%;
        position: relative;
        height: 100%;
        z-index: 99;

        h1,
        .h1 {
            margin-bottom: 40px;

            @include respond-below(md) {
                margin-bottom: 25px;
            }
        }
        &.align-content-center{
            align-items: center;
        }

        @include respond-below(md) {
            .text-black {
                color: $color--white;
            }

            padding-top: 15vh;
        }

        @include respond-above(md) {
            padding-top: 140px;
        }        
    }

    .btn {
        margin-top: 30px;
    }
}

.inner-herobanner {
    position: relative;

    img {
        margin: auto;
        display: block;
        width: 100%;
        max-height: 100vh;
        object-fit: cover;
        object-position: top center;
    }
    &.overlay{
        &:after {
            position: absolute;
            content: '';
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            background-color: $color--grey-1;
            opacity: 0.70;
        }
        &.opacity-3  {
            &:after {
                opacity: 0.3;
            }
        }
    }

    &__caption {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 3;

        @include respond-below(md) {
            top: 30px;
        }
    }
}